/* eslint class-methods-use-this: ["error", { "exceptMethods": ["removeItem"] }] */

import React, { Component } from 'react';
import bellIcon from './img/bell-icon.png';
import phoneIcon from './img/phone-icon.png';
import style from './shopping-list.css';

class ShoppingList extends Component {
  constructor(props) {
    super(props);

    this.state = {
      items: [],
    };

    this.removeItem = this.removeItem.bind(this);
  }

  componentDidMount() {
    setInterval(() => this.getShoppingList(), 1000);
  }

  async getShoppingList() {
    fetch('/api/shoppingList')
      .then(response => response.json())
      .then(data => {
        this.setState({
          items: data.data,
        });
      });
  }

  removeItem(event) {
    fetch('/api/shoppingList', {
      method: 'delete',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        text: event.currentTarget.childNodes[1].nodeValue,
      }),
    })
      .then(response => response.json())
      .then(data => {
        console.log(data);
      });
  }

  render() {
    return (
      <>
        <div className={style.heading}>
          <div className={style.one}>Shopping List</div>
          <div className={style.icon}>
            <img width="35px" src={phoneIcon} alt="call" />{' '}
          </div>
          <div className={style.icon}>
            <img width="35px" src={bellIcon} alt="notify" />{' '}
          </div>
        </div>
        <div className={style.itemWrapper}>
          {this.state.items.map((shoppingItem, index) => (
            <div
              key={shoppingItem.key}
              className={style.item}
              onClick={this.removeItem}
              onKeyPress={this.removeItem}
              role="button"
              tabIndex={index}
            >
              <div className={style.itemOwner}>
                {shoppingItem.ownerName}
                <span className={style.dot} />
              </div>
              {shoppingItem.text}
            </div>
          ))}
        </div>
      </>
    );
  }
}

ShoppingList.propTypes = {};

export default ShoppingList;
